.mobile-burger {
  transition: all .4s;
  flex-direction: column;
  grid-gap: 7px;
  align-items: flex-end;

  span {
    transition: all .4s;
    width: 30px;
    height: 3px;
    background-color:#77ac57;
    border-radius: 4px;

    &:nth-child(2) {
      width: 20px;
    }

    &:nth-child(3) {
      width: 10px;
    }
  }

  &.active {

    span {
      width: 30px;
      height: 3px;
      background-color:#77ac57;
      border-radius: 4px;

      &:nth-child(1) {
        transform: rotate(45deg) translate(8px, 5px);
      }

      &:nth-child(2) {
        width: 0px;
      }

      &:nth-child(3) {
        width: 30px;
        transform: rotate(-45deg) translate(9px, -6px);
      }
    }
  }
}

.header__item {
  transition: all .4s;
}

.header__item.active {
  transform: translate(0);
}



@media (min-width: 280px) and (max-width: 768px) {
  .header__item {
    position: fixed;
    transform: translateX(-110%);
    width: 100%;
    left: 0;
    top: 60px;
    height: calc(100vh - 60px);
    background: rgba(39, 39, 39, 0.911);
    z-index: 50;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    grid-gap: 30px;
    padding: 40px 20px;
    overflow-y: scroll;

    nav {
      flex-grow: 1;
    }
  }

  header {
    position: relative;
    z-index: 51;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }

  .mobile-burger {
    display: flex;
  }

  .nav__list {
    display: grid;
    grid-gap: 20px;
  }

  .nav__link, .header-phones__link {
    font-size: 14px;
  }

  .select__current {
    font-size: 12px;
  }

  .select__item span {
    font-size: 12px;
  }

  .select__header {
    height: 25px;
  }
}

@media (min-width: 768px) and (max-width: 820px) {
  .header__item  {
    grid-gap: 20px;

    .header-phones {
      padding-left: 30px;
    }
  }

  .nav__list {
    grid-gap: 30px;
  }
}



@media (min-width: 820px) and (max-width: 1000px) {
  .header__item  {
    grid-gap: 30px;
  }
}
